@import 'assets/styles/index.scss';

.addForm {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 40px;
}

.addFormInputContainer {
  display: flex;

  justify-content: center;
  align-items: center;
  @include mq(mob-only) {
    flex-direction: column;
  }
  @include mq(tab) {
    flex-wrap: wrap;
  }
}

.categoryLabel {
  position: relative;
}

.addFormInputCategory {
  padding: 4px 20px 8px 20px;
  margin-bottom: 40px;
  font-size: 18px;
  line-height: 27px;
  border: 0;
  border-bottom: 1px solid #e0e0e0;
  outline: 0;

  @include mq(mob-only) {
    width: 280px;
    height: 32px;
  }
  @include mq(tab) {
    width: 394px;
    height: 32px;
  }

  animation: scaleTitle 0.3s ease 1 alternate;
  @keyframes scaleTitle {
    0% {
      transform: scaleX(0);
    }

    100% {
      transform: scaleX(1);
    }
  }
}

.openMenuBtn {
  position: absolute;
  top: 0;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  border: 0;
  cursor: pointer;

  &Icon {
    width: 22px;
    height: 20px;
  }
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 22px;
    height: 20px;
  }
}

.addFormInputSum {
  padding: 4px 20px 8px 20px;

  font-size: 18px;
  line-height: 27px;
  border: 0;
  border-bottom: 1px solid #e0e0e0;
  outline: 0;
  @include mq(mob-only) {
    margin-bottom: 40px;
    width: 280px;
    height: 32px;
  }
  @include mq(tab) {
    text-align: center;
    width: 190px;
    margin-right: 24px;
  }
}

.addFormInputDate {
  font-size: 18px;
  line-height: 27px;
  border: 0;
  border-bottom: 1px solid #e0e0e0;
  outline: 0;
  @include mq(mob-only) {
    padding: 4px 20px 8px 20px;
    margin-bottom: 40px;
    width: 280px;
    height: 32px;
  }
  @include mq(tab) {
    padding: 4px 14px 8px 20px;
    width: 180px;
  }
}

.dataBox {
  position: relative;
}

.dataBtn {
  position: absolute;
  top: 0;
  right: 20px;
  display: flex;

  justify-content: center;
  align-items: center;
  background-color: #fff;
  color: #4a56e2;
  border: 0;
  cursor: pointer;

  &Icon {
    width: 24px;
    height: 24px;
  }
  @include mq(mob-only) {
    top: 0;
    right: 20px;
  }
  @include mq(tab) {
    top: 4px;
    right: 14px;
  }
}

.addFormTextarea {
  resize: none;

  padding: 4px 20px 2px 20px;
  font-size: 18px;
  line-height: 27px;
  border: 0;
  border-bottom: 1px solid #e0e0e0;
  outline: 0;

  &::-webkit-scrollbar {
    width: 4px;
  }

  &::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    border-radius: 10px;
  }

  &::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.5);
  }
  @include mq(mob-only) {
    width: 280px;
    height: 84px;
  }
  @include mq(tab) {
    width: 394px;
    height: 40px;
    margin-top: 40px;
  }
}

