.error {
  position: absolute;
  bottom: -15px;
  left: 0;
  font-size: 9px;
  color: red;
  @include mq(mob-only) {
    max-width: 280px;
    max-height: 10px;
  }
}
.sumBox {
  position: relative;
}

.addBtn {
  @extend %btnGreen;

  width: 300px;
  padding: 13px 0;
  border-radius: 20px;
  text-transform: uppercase;
}
